<template>
  <div class="vx-scenarist">
    <vx-header :showback="1" :showreset="1" :title="$t('common.scenarist')" @reset="reload"></vx-header>
    <div class="vx-scenarist-container" @click.stop="hideBtns">
      <div class="list-container">
        <div class="out-swiper-container swiper-container-horizontal">
          <div class="swiper-wrapper">
            <div class="swiper-slide swiper-container-source">
              <div class="swiper-wrapper">
                <div class="swiper-slide" :class="{active: zIndexMenuOption.swiperActiveIndex === i}" v-if="i<sourceListLen" v-for="(o,i) in inputSources" :key="i" :data-index="i" v-touch.pan.tap="handleSource">
                  <div class="device_icon" :class="mapPicture[o.type] + (o.status===0 ? '':'_active')"></div>
                  <div class="title">{{o.name}}</div>
                </div>
                <div class="swiper-slide hide" v-for="k in sourceLen"></div>
              </div>
              <div class="swiper-pagination"></div>
            </div>
            <div class="swiper-slide swiper-container-osd">
              <div class="swiper-wrapper">
                <div class="swiper-slide addosd" @click="$router.push({ name: 'osd' })">
                </div>
                <div class="swiper-slide" :class="{active: zIndexMenuOption.swiperActiveIndex === i}" v-if="i>=sourceListLen" v-for="(o,i) in inputSources" :key="i" :data-index="i" v-touch.pan.tap="handleSource">
                  <div class="device_icon" :class="mapPicture[o.type] + (o.status===0 ? '':'_active')"></div>
                  <div class="title">{{o.name}}</div>
                </div>
                <div class="swiper-slide hide" v-for="k in osdLen"></div>
              </div>
              <div class="swiper-pagination"></div>
            </div>
          </div>
        </div>
        <div class="sourcetabs">
          <div class="tab" :class="{'selected-tab':activeTab==1}" @click="selecteTad(1)">{{$t('common.source')}}</div>
          <div class="tab" :class="{'selected-tab':activeTab==2}" @click="selecteTad(2)">OSD</div>
        </div>
      </div>
      <div class="detail-container">
        <div class="detail-container-item">
          <div class="title">{{$t('scenarist.showSize')}}</div>
          <div class="value" v-if="layer.showWidth!==''">{{layer.showWidth}}x{{layer.showHeight}}</div>
        </div>
        <div class="detail-container-item">
          <div class="title">{{$t('scenarist.winSize')}}</div>
          <div class="value" v-if="layer.width!==''">{{layer.width}}x{{layer.height}}</div>
        </div>
        <div class="detail-container-item">
          <div class="title">{{$t('scenarist.winPosition')}}</div>
          <div class="value" v-if="layer.x!==''">({{layer.x}},{{layer.y}})</div>
        </div>
      </div>
      <div class="view">
        <div class="layer" ref="source-window" :class="{dangrous: enteringSource.fullSourceStatus}">
          <div class="rowlines">
            <div class="row-line" v-for="(item, index) in lay.cols" :key="index"></div>
          </div>
          <div class="col">
            <div class="column-line" v-for="(item, index) in lay.rows" :key="index"></div>
          </div>
          <div class="source-item-use" v-for="(source, index) in openedSources" :key="index" :data-index="index" :class="{'align-right': source.isAlignRight, active: index === zIndexMenuOption.activeSource,['bgm-' + source.colorIndex]: true, 'replace-outline': source.replaceStatus}" :style="{left: source.x + 'px', top: source.y + 'px', right: source.x + 'px', width: source.w + 'px', height: source.h + 'px', 'z-index': source.zIndex, 'background-position': Math.abs(Math.min(source.isAlignRight ? sourceWindow.w - source.x - source.w : source.x, 0)) + 'px ' + Math.abs(Math.min(source.y, 0)) + 'px', 'background-size': Math.min(sourceWindow.w - (source.isAlignRight ? sourceWindow.w - source.x - source.w : source.x), sourceWindow.w) + 'px ' + Math.min(sourceWindow.h - source.y, sourceWindow.h) + 'px'}" v-touch.pinch.press.tap.pan="handleSourceInWindow">
            <div class="device_icon" :class="mapPicture[source.type] + (source.status===0 ? '':'_active')"></div>
            <div class="title">{{source.name}}</div>
            <div :class="{'lock': source.isLock}"></div>
            <div class="move-btn btnLt" v-show="zIndexMenuOption.activeSource === index && !source.isLock&&source.sourceType!=='osd'" v-touch.tap.pan="handleSourceLT" :data-index="index"></div>
            <div class="move-btn btnRt" v-show="zIndexMenuOption.activeSource === index && !source.isLock" v-touch.tap="deleteSource" :data-index="index"></div>
            <div class="move-btn btnRb" v-show="zIndexMenuOption.activeSource === index && !source.isLock&&source.sourceType!=='osd'" v-touch.pan="scaleR" :data-index="index"></div>
            <div class="move-btn btnLb" v-show="zIndexMenuOption.activeSource === index && !source.isLock&&source.sourceType!=='osd'" v-touch.pan="scaleL" :data-index="index"></div>
            <div v-show="source.replaceStatus" class="replace-outline"></div>
          </div>
          <div class="operator-btns" ref="operator-btns" v-if="zIndexMenuOption.operateBtnGroupIndex !== -1" :style="{left: zIndexMenuOption.langPressX + 'px', top: zIndexMenuOption.langPressY - 50 + 'px'}">
            <span class="operator-btn" @click="cutSource">{{$t('common.cut')}}</span>
            <span class="operator-btn" @click="onLayout">{{$t('common.layout')}}</span>
          </div>
        </div>
        <p class="note">{{$t('scenarist.longpress')}}</p>
      </div>
      <div class="btn-group">
        <div class="btn" @click="closeWindows">
          <img src="../../assets/common/clear.png" slot="icon" />
        </div>
        <div class="btn" @click="reset">
          <img src="../../assets/common/reset.png" slot="icon" />
        </div>
        <div class="btn" @click="prev">
          <img src="../../assets/common/pre.png" slot="icon" />
        </div>
        <div class="btn" @click="next">
          <img src="../../assets/common/next.png" slot="icon" />
        </div>
        <div class="btn" @click="first">
          <img src="../../assets/common/first.png" slot="icon" />
        </div>
        <div class="btn" @click="last">
          <img src="../../assets/common/last.png" slot="icon" />
        </div>
        <div class="btn" @click="lock">
          <img src="../../assets/common/lock.png" slot="icon" v-if="zIndexMenuOption.isLock" />
          <img src="../../assets/common/open-lock.png" slot="icon" v-else />
        </div>
      </div>
      <div class="source-entering" v-show="enteringSource.show" :style="{left: enteringSource.x + 'px', top: enteringSource.y + 'px', width: enteringSource.w + 'px', height: enteringSource.h + 'px', 'z-index': enteringSource.zIndex}">
        <div class="device_icon" :class="mapPicture[enteringSource.type] + (enteringSource.status===0 ? '':'_active')"></div>
        <div class="title">{{enteringSource.name}}</div>
      </div>
    </div>
    <vx-tcnbtn v-if="zIndexMenuOption.isShowTake"></vx-tcnbtn>
  </div>
</template>

<script>
  import htp from '../../api/http';
  import urls from '../../api/apiurl';
  import Swiper from 'swiper';
  import { Indicator } from 'mint-ui';
  import mixin from './scenarist-mixin';
  import helper from './scenaristHelper';
  import { mapGetters } from 'vuex';
  import { getSen, getLoc } from '../../utils';

  export default {
    mixins: [mixin, helper],
    beforeRouteEnter(from, to, next) {
      if(getSen('isHScreen')) {
        next({ name: 'scenarist-h' });
      }
      next();
    },
    mounted() {
      setTimeout(() => {
        this.sourceWindow = this.getPosition(this.$refs['source-window']);
        this.initLayer();
        this.zIndexMenuOption.isShowTake = true;
      }, 1000);
    },
    watch: {
      getIsHScreen(val) {
        if(val) {
          this.$router.replace({ name: 'scenarist-h' });
        }
      }
    },
    computed: {
      ...mapGetters(['getIsHScreen', 'getScreenStructure'])
    },
    methods: {
      /**初始化窗体 */
      initLayer() {
        if(!window.offline) {
          Indicator.open({
            text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
            spinnerType: 'triple-bounce'
          });
        }
        htp.all(
          htp.get(urls.getOutputResolutionRatio), //获取输出分辨率
          htp.get(urls.getInputSourceList),
          htp.get(urls.getValidOsdList),
        ).then(res => {
          if(res[0].status === 10000000 && res[1].status === 10000000 && res[2].status === 10000000) {
            this.lay.cols = this.getScreenStructure.screenCols;
            this.lay.rows = this.getScreenStructure.screenRows;
            this.layer.showWidth = res[0].data.width;
            this.layer.showHeight = res[0].data.height;

            let osdSource = res[2].data.pictureList;
            osdSource.forEach((source, i) => {
              if(source.type === 1) {
                source.type = 13;
              } else {
                source.type = 12;
              }
              source.sourceType = 'osd';
              source.status = 0;
              source.name = 'OSD' + (source.id + 1);
            });

            let normalSource = res[1].data.inputSourceList;
            this.osdlistLen = osdSource.length;
            this.sourceListLen = normalSource.length;
            this.sourceLen = normalSource.length % this.pageNnm === 0 ? 0 : this.pageNnm - normalSource.length % this.pageNnm;
            this.osdLen = (osdSource.length + 1) % this.pageNnm === 0 ? 0 : this.pageNnm - (osdSource.length + 1) % this.pageNnm;
            normalSource.forEach(source => {
              source.sourceType = 'normal';
            });
            this.inputSources = normalSource.concat(osdSource);

            this.inputSources.forEach(source => {
              if(source.sourceType === 'osd') {
                source.h = source.height * this.getVirtualDom.hScale;
                source.w = source.width * this.getVirtualDom.wScale;
              } else {
                source.h = this.constOption.defaultSourceHeight * this.getVirtualDom.hScale;
                source.w = this.constOption.defaultSourceWidth * this.getVirtualDom.wScale;
              }
            });
            let vm = this;
            this.$nextTick(() => {
              this.outSwiper = new Swiper('.out-swiper-container', {
                spaceBetween: 0,
                onSlideNextEnd(swiper) {
                  vm.activeTab = swiper.activeIndex + 1;
                },
                onSlidePrevEnd(swiper) {
                  vm.activeTab = swiper.activeIndex + 1;
                }
              });
              if(this.sourceListLen > this.pageNnm) {
                this.sourceSwiper = new Swiper('.swiper-container-source', {
                  spaceBetween: 5,
                  slidesPerView: vm.pageNnm,
                  slidesPerGroup: vm.pageNnm,
                  nested: true,
                  pagination: '.swiper-pagination'
                });
              } else {
                this.sourceSwiper = new Swiper('.swiper-container-source', {
                  spaceBetween: 5,
                  slidesPerView: vm.pageNnm,
                  slidesPerGroup: vm.pageNnm,
                  nested: true
                });
              }

              if(this.osdlistLen <= this.pageNnm - 1) {
                this.osdSwiper = new Swiper('.swiper-container-osd', {
                  spaceBetween: 5,
                  slidesPerView: vm.pageNnm,
                  slidesPerGroup: vm.pageNnm,
                  nested: true
                });
              } else {
                this.osdSwiper = new Swiper('.swiper-container-osd', {
                  spaceBetween: 5,
                  slidesPerView: vm.pageNnm,
                  slidesPerGroup: vm.pageNnm,
                  nested: true,
                  pagination: '.swiper-pagination'
                });
              }
            });
            htp.all(
              htp.get(urls.getWinList),
              htp.get(urls.getOsdParam)
            ).then(res => {
              Indicator.close();
              if(res[0].status === 10000000 && res[1].status === 10000000) {
                this.winList = res[0].data.winList;
                this.osdList = res[1].data.osdList;

                this.getInputSourceList();
                this.getOsdList();
                this.sortOpenedSource();
                let openedWins = this.winList.filter(layer => {
                  return layer.status === 1;
                });
                if(openedWins.length) {
                  this.zIndexMenuOption.zIndex = openedWins.max('priority').priority + 1;
                  this.zIndexMenuOption.isLock = openedWins[0].isLocked;
                }

                this.openedSources.forEach(sourec => {
                  if(sourec.sourceType !== 'osd') {
                    this.colorHandler();
                    sourec.colorIndex = vm.colorIndex;
                    vm.colorIndex++;
                  } else {
                    sourec.colorIndex = 8;
                  }
                });
              }
            });
          }
        });
      }
    }
  };
</script>

<style lang="less" scoped>
  .vx-scenarist-container {
    width: 100%;
    height: 100%;
    padding-top: 80px;
    position: relative;
    overflow: hidden;
    background-color: #171719;
  }

  /*设备详细信息样式*/
  .detail-container {
    display: flex;
    height: 180px;
    background-color: #242428;
    align-items: center;
    &-item {
      flex: 1;
      .title,
      .value {
        text-align: center;
        color: #f8f8f8;
        font-size: 30px;
      }
      .title {
        color: #bababa;
        font-size: 26px;
        padding-bottom: 20px;
      }
    }
  }

  /*窗口样式*/
  .view {
    width: 100%;
    margin-top: 80px;
    padding: 42px 30px 0;
    overflow: hidden;
    .layer {
      height: 410px;
      outline: 1px solid #999999; /*no*/
      position: relative;
      border-radius: 6px;
      .rowlines {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        position: absolute;
      }
      .col {
        display: flex;
        width: 100%;
        height: 100%;
        position: absolute;
      }
      .row-line,
      .column-line {
        flex: 1;
      }
      .row-line {
        width: 100%;
        height: 0;
        border-bottom: 1px dotted #999; /* no*/
        &:last-child {
          border-bottom: 0;
        }
      }
      .column-line {
        width: 0;
        height: 100%;
        border-right: 1px dotted #999; /* no*/
        &:last-child {
          border-right: 0;
        }
      }
    }
  }

  .btn-group {
    height: 80px;
    line-height: 80px;
    margin-top: 10px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .btn {
      height: 80px;
      width: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 36px;
      }
    }
  }

  .list-container {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 auto;
    background-color: #141416;
    border-top: 1px solid #383940; /*no*/
    .sourcetabs {
      width: 100%;
      height: 80px;
      display: flex;
      align-items: center;
      background-color: #242428;
      .tab {
        width: 120px;
        height: 80px;
        line-height: 80px;
        font-size: 28px;
        color: #ddd;
        border-right: 1px solid #33343a;
      }
      .selected-tab {
        background-color: #1c1c1e;
      }
    }
  }

  .note {
    color: #585858;
    font-size: 12px; /*no*/
    text-align: left;
    margin: 0;
    padding-top: 10px;
  }

  /*swiper样式*/
  .out-swiper-container {
    position: relative;
    overflow-x: hidden;
    margin: 10px 5px 0; /*no*/
    background-color: #141416;
    > .swiper-wrapper {
      position: relative;
      width: 100%;
      height: 150px;
      z-index: 1;
      display: flex;
      box-sizing: content-box;
      > .swiper-slide {
        font-size: 18px;
        flex-shrink: 0;
        width: 100%;
        height: 150px;
        position: relative;
        border-radius: 5px; /*no*/
      }
    }
  }

  .swiper-container-source,
  .swiper-container-osd {
    height: 100%;
    position: relative;
    overflow-x: hidden;
    background-color: #141416;
    > .swiper-wrapper {
      position: relative;
      width: 100%;
      height: 110px;
      z-index: 1;
      display: flex;
      box-sizing: content-box;
      > .swiper-slide {
        font-size: 18px;
        background: #41434b;
        flex-shrink: 0;
        width: 100%;
        height: 100%;
        position: relative;
        border-radius: 5px; /*no*/
        &.active {
          border: 1px solid #e67007; /*no*/
          background-color: #2e2e34;
        }
      }
      .addosd {
        background: ~"url('../../assets/common/addosd.png')" no-repeat center center !important;
        background-size: 100% 100% !important;
      }
    }
  }

  @media (max-height: 480px) {
    .view {
      padding-top: 45px;
      margin-top: 0;
    }
  }
</style>

<style lang="less">
  .swiper-pagination {
    padding-top: 10px;
    text-align: center;
    z-index: 10;
    span {
      width: 16px;
      height: 16px;
      display: inline-block;
      border-radius: 100%;
      background: ~"url('../../assets/common/dot.png')" no-repeat center center;
      background-size: 12px 12px;
      margin: 0 10px;
    }
    span.swiper-pagination-bullet-active {
      background: ~"url('../../assets/common/dot-m.png')" no-repeat center center;
      width: 16px;
      height: 16px;
      background-size: 16px 16px;
    }
  }
  .hide {
    background: #141416 !important;
    border: 0 !important;
  }
</style>



